import { createWebHashHistory, createRouter } from 'vue-router';
import Home from '../pages/Index.vue';
import About from '../pages/About.vue';
import Contact from '../pages/Contact.vue';

import Matlab from '../pages/projects/MatlabProjects';
import Python from '../pages/projects/PythonProjects.vue';
import MachineLearning from '../pages/projects/MachineLearningProjects.vue';
import ComputerVision from '../pages/projects/ComputerVisionProjects.vue';

const routes = [
  {
    path: "/",
    name: "Home",
    component: Home,
    props: {
      color: 'blue'
    }
  },
  {
    path: "/about",
    name: "About",
    component: About,
    props: {
      color: 'blue'
    }
  },
  {
    path: "/contact",
    name: "Contact",
    component: Contact,
    props: {
      color: 'blue'
    }
  },
  {
    path: "/projects/matlab",
    name: "Matlab",
    component: Matlab,
    props: {
      color: 'green'
    }
  },
  {
    path: "/projects/python",
    name: "Python",
    component: Python,
    props: {
      color: 'blue'
    }
  },
  {
    path: "/projects/computer-vision",
    name: "Computer Vision",
    component: ComputerVision,
    props: {
      color: 'pink'
    }
  },
  {
    path: "/projects/machine-learning",
    name: "MachineLearning",
    component: MachineLearning,
    props: {
      color: 'red'
    }
  },
];

const router = createRouter({
  history: createWebHashHistory(),
  base: '/',
  routes,
});

export default router;